import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'log_documento-dados',   templateUrl: 'log_documento_dados.component.html' }) export class Log_documentoDadosComponent implements OnInit {       displayMode: Log_documentoDadosDisplayModeEnum;     displayModeEnum = Log_documentoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Log_documentoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Log_documentoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Log_documentoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Log_documentoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


