import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Log_documento } from '../../../modelo/Log_documento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'log_documento-dados-details',   templateUrl: 'log_documento_dadosDetails.component.html' }) export class Log_documentoDadosDetailsComponent implements OnInit {    log_documento: Log_documento;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.log_documento.Log_documento";                 let id = +params['id'];         this.log_documento = new Log_documento();         this.log_documento.cd_log_documento = id;         o.objetoJson = this.log_documento;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a log_documento. '; }      this.log_documento = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


